<template>
  <div class="layer-pane">
    <section>
      <div
        class="flex items-center justify-between gap-5"
        v-for="layer in totalLayer"
        :key="layer"
      >
        <div
          class="primary-btn layer-btn"
          :class="{ 'de-avtivated': activatedLayer !== layer }"
          @click="selectLayer(layer)"
        >
          Layer {{ layer }}
        </div>
        <i
          class="el-icon-delete-solid delete-layer-btn"
          @click="deleteLayer(layer)"
        />
      </div>

      <div class="primary-btn add-layer-btn" @click="addLayer">
        + Layer {{ totalLayer + 1 }}
      </div>
    </section>

    <section>
      <div class="primary-btn func-btn" @click="importNodes">Import Nodes</div>
      <input
        class="hidden"
        @input="importCSV"
        ref="nodeImport"
        type="file"
        name=""
        id="import"
      />

      <div class="primary-btn func-btn disabled" v-if="!nodes.length">
        Export
      </div>
      <download-csv :data="nodes" v-if="nodes.length">
        <div class="primary-btn func-btn" id="export-btn">Export</div>
      </download-csv>
    </section>

    <ImportWarningModalVue
      v-if="showImportWarningModal"
      @close="showImportWarningModal = false"
      @confirm="$refs['nodeImport'].click()"
    />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import csv2json from 'csvjson-csv2json'
import { forEach, filter } from 'lodash'
import NetNode from '@/factory/node'
// import NetLink from '@/factory/link'
import ImportWarningModalVue from '@/components/LayerPane/ImportWarningModal.vue'

export default {
  name: 'LayerPane',
  components: { ImportWarningModalVue },
  computed: {
    ...mapState('network', ['nodes', 'links']),
    ...mapState('layer', ['totalLayer', 'activatedLayer']),
    preLayer() {
      if (this.activatedLayer === 0) return 0
      return this.activatedLayer - 1
    },
    nextLayer() {
      return this.activatedLayer + 1
    },
  },
  data: () => ({
    showImportWarningModal: false,
  }),

  methods: {
    addLayer() {
      this.$store.commit('layer/SET_TOTAL_LAYER', this.totalLayer + 1)
      this.$store.commit('layer/SET_ACTIVATED_LAYER', this.totalLayer)
    },

    selectLayer(layer) {
      this.$store.commit('layer/SET_ACTIVATED_LAYER', layer)
    },

    parseCSVData(data) {
      console.log(data)
      const list = []
      forEach(data, (item) => {
        const node = new NetNode(
          `1-${item.Label}`,
          item.Label,
          Number(this.closeness) || 0,
          1
        )
        list.push(node)
      })

      this.$store.commit('network/SET_NODES', list)
      this.$message.success('Import nodes success')
    },

    importCSV(e) {
      let file = e.target.files[0]
      let reader = new FileReader()
      reader.onload = () => {
        const csvArray = csv2json(reader.result, { parseNumbers: true })
        this.parseCSVData(csvArray)
      }
      reader.readAsText(file)
    },

    deleteLayer(layer) {
      const nodesRef = JSON.parse(JSON.stringify(this.nodes))
      const linksRef = JSON.parse(JSON.stringify(this.links))

      // 刪除當前層與大於層的節點
      const layerNodes = filter(
        nodesRef,
        (node) => node.layer !== layer && node.layer < layer
      )
      this.$store.commit('network/SET_NODES', layerNodes)

      // 刪除當前層與大於層的連線
      const layerLinks = filter(linksRef, (link) => {
        return (
          link.source.layer !== layer &&
          link.target.layer !== layer &&
          link.target.layer < layer &&
          link.source.layer < layer
        )
      })
      this.$store.commit('network/SET_LINKS', layerLinks)

      // 刪除當前層與大於的層

      this.$store.commit('layer/SET_TOTAL_LAYER', layer - 1)
      this.$store.commit('layer/SET_ACTIVATED_LAYER', layer - 1)

      // FIXME 刪除後 index 不會更新導致連線有問題
    },

    importNodes() {
      this.showImportWarningModal = true
      // this.$refs['nodeImport'].click()
    },
  },
}
</script>

<style scoped lang="postcss">
.layer-pane {
  --tw-bg-opacity: 1;
  background-color: rgba(43, 48, 59, var(--tw-bg-opacity));
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  --tw-text-opacity: 1;
  color: rgba(156, 163, 175, var(--tw-text-opacity));
  grid-gap: 0.5rem;
  gap: 0.5rem;
  height: 100%;
  padding: 1.25rem;
  width: 100%;
}
.layer-btn {
  --tw-text-opacity: 1;
  color: rgba(55, 65, 81, var(--tw-text-opacity));
  width: 100%;
}
.layer-btn:is(.de-avtivated):hover {
  --tw-bg-opacity: 0.7;
}
.de-avtivated {
  --tw-bg-opacity: 1;
  background-color: rgba(156, 163, 175, var(--tw-bg-opacity));
}
.add-layer-btn {
  background-color: transparent;
  border-style: dashed;
  border-width: 1px;
}
.add-layer-btn:hover {
  --tw-bg-opacity: 1;
  background-color: rgba(110, 231, 183, var(--tw-bg-opacity));
  --tw-text-opacity: 1;
  color: rgba(55, 65, 81, var(--tw-text-opacity));
}
.func-btn:hover {
  --tw-bg-opacity: 0.5;
}
.func-btn {
  --tw-text-opacity: 1;
  color: rgba(55, 65, 81, var(--tw-text-opacity));
}
.delete-layer-btn {
  cursor: pointer;
  transition: 0.3s;
}
.delete-layer-btn:hover {
  --tw-text-opacity: 1;
  color: rgba(251, 113, 133, var(--tw-text-opacity));
}
.disabled {
  --tw-bg-opacity: 1;
  background-color: rgba(156, 163, 175, var(--tw-bg-opacity));
  cursor: not-allowed;
}
